/* heebo-100 - hebrew_latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: "Heebo";
  font-style: normal;
  font-weight: 100;
  src: url("assets/fonts/heebo-v21-hebrew_latin-100.eot"); /* IE9 Compat Modes */
  src: url("assets/fonts/heebo-v21-hebrew_latin-100.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("assets/fonts/heebo-v21-hebrew_latin-100.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("assets/fonts/heebo-v21-hebrew_latin-100.woff") format("woff"),
    /* Modern Browsers */ url("assets/fonts/heebo-v21-hebrew_latin-100.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("assets/fonts/heebo-v21-hebrew_latin-100.svg#Heebo") format("svg"); /* Legacy iOS */
}
