.site-header {
  border-bottom: 1px solid #97c93c;
  background-color: rgb(255, 255, 255);
  transition: all 0.55s ease-in-out;
  height: 84px;
  width: 100%;
  position: sticky;
  top: 0;
  z-index: 100;
}
.site-header .main-navigation {
  transform: translateX(100%);
  transition: transform 0.35s ease-in-out;
  position: fixed;
  width: 80%;
  height: 100%;
  right: 0;
  left: 0;
  top: 0;
  background-color: #ffffff;
  z-index: 100;
}
.site-header .main-navigation ul {
  margin: 0;
  padding: 1rem 0;
}
.site-header .main-navigation ul li {
  list-style: none;
  position: relative;
}
.site-header .main-navigation ul li a {
  border-bottom: 1px solid #eeeeee;
  padding: 0.5rem 1.5rem;
  display: block;
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  color: #336138;
}
.site-header .main-navigation ul li .sub-menu-arrow {
  display: none;
}
.site-header .main-navigation ul li ul {
  padding: 0;
  max-height: 0;
  overflow: hidden;
}
.site-header .main-navigation ul li ul.menu-open {
  max-height: 100%;
}
.site-header .main-navigation ul li ul li a {
  position: relative;
  font-size: 14px;
}
.site-header .main-navigation ul li ul li a::before {
  content: " --- ";
  margin-left: 5px;
}
.site-header .main-navigation ul li.menu-item-has-children > .sub-menu-arrow {
  position: absolute;
  left: 10px;
  top: 7px;
  display: block;
  width: 25px;
  text-align: center;
}
.site-header .main-navigation ul li.menu-item-has-children > .sub-menu-arrow.flip {
  transform: scaleY(-1);
}
.site-header .main-navigation ul li.current-menu-item > a {
  color: #97c93d;
}
.site-header .main-navigation.menu-open {
  transform: translateX(0);
}
.site-header .social-search {
  display: none;
  align-items: center;
}
.site-header .social-search a {
  text-decoration: none;
}
.site-header .social-search .instagram {
  margin: 0 4px 0 10px;
}
.site-header .social-search svg {
  max-width: 20px;
  height: auto;
}
.site-header .social-search .proinput {
  width: 100% !important;
}
.site-header form {
  border: 1px solid #97c93d;
  border-radius: 17px;
}
.site-header form input[type=search] {
  background-color: transparent;
  border: none;
}
.site-header form input[type=search]:focus {
  box-shadow: none;
}
.site-header form .search-ico {
  position: relative;
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='19' height='19' viewBox='0 0 19 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E %3Cpath d='M17.5 17.5L13.7617 13.755L17.5 17.5ZM15.8333 8.74999C15.8333 10.6286 15.0871 12.4303 13.7587 13.7587C12.4303 15.087 10.6286 15.8333 8.75 15.8333C6.87139 15.8333 5.06971 15.087 3.74133 13.7587C2.41295 12.4303 1.66667 10.6286 1.66667 8.74999C1.66667 6.87137 2.41295 5.0697 3.74133 3.74132C5.06971 2.41293 6.87139 1.66666 8.75 1.66666C10.6286 1.66666 12.4303 2.41293 13.7587 3.74132C15.0871 5.0697 15.8333 6.87137 15.8333 8.74999V8.74999Z' stroke='%23336138' stroke-width='2' stroke-linecap='round'/%3E %3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
}
.site-header form .search-ico input[type=submit] {
  background-color: transparent;
  border: none;
  opacity: 0;
  width: 100%;
  height: 100%;
}
.site-header form.search-form-m {
  max-width: 90%;
  margin: 0 auto;
}
.site-header.sticky {
  background-color: rgb(255, 255, 255);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
}

.site-main {
  padding-top: 30px;
