.hero-banner .carousel {
  height: 150px;
}
.hero-banner .carousel .swiper-wrapper .swiper-slide {
  position: relative;
}
.hero-banner .carousel .swiper-wrapper .swiper-slide img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
